<template>
  <transition name="slide">
    <div class="user-wrap">
      <i class="iconfont icon-back icon-fanhui" @click="$router.back()"></i>
      <div class="switches-box">
        <div class="title">个人中心</div>
      </div>
      <div class="avator">
        <img src="~@/assets/rabit.jpg" alt="">
        <div class="change">更换头像</div>
        <div class="text">133***8562</div>
      </div>
      <ul class="fun-list">
        <li @click="handle('/focus-list')">我的关注</li>
        <li @click="handle('/like-list')">我的点赞</li>
        <li @click="handle('/discussion')">我的评论</li>
        <li @click="handle('/collection')">收藏中心</li>
      </ul>
    </div>
  </transition>
</template>

<script>
  export default {
    data() {
      return {
      }
    },
    computed: {
    },
    methods: {
      handle(url) {
        this.$router.push(url)
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../common/scss/variable";
  .avator {
    text-align: center;
    img {
      border-radius: 50%;
    }
    .text {
      color: #666;
      margin-top: 10px;
    }
    .change {
      border: 1px solid #666;
      width: fit-content;
      padding: 4px 6px;
      border-radius: 4px;
      margin: 10px auto 0;
      font-size: 12px;
    }
  }
  .fun-list {
    padding: 50px 0;
    margin: 0 20px;
    li {
      line-height: 46px;
      height: 46px;
      font-size: 16px;
      color: #666;
      border-bottom: 1px solid #eaeaea;
      position: relative;
      &::after {
        content: '';
        display: inline-block;
        background: url('~@/assets/rightgreyrow.png');
        background-size: 100% 100%;
        width: 6px;
        height: 12px;
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
      }
    }
  }
  .user-wrap {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: $color-background;
    // &.slide-enter-active, &.slide-leave-active {
    //   transition: all .3s;
    // }
    // &.slide-enter, &.slide-leave-to {
    //   transform: translate3d(100%, 0, 0);
    // }
    .title {
      height: 45px;
      line-height: 45px;
      text-align: center;
    }
    .icon-back {
      position: absolute;
      top: 0;
      left: 6px;
      padding: 10px;
      font-size: $font-size-large;
      color: $color-theme;
    }
    .switches-box {
      margin: 0 0 30px;
    }
    .play-btn {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 135px;
      margin: 0 auto;
      padding: 7px 0;
      color: $color-text-l;
      border: 1px solid $color-text-l;
      border-radius: 100px;
      box-sizing: border-box;
      .icon-play {
        margin-right: 6px;
        font-size: $font-size-medium-x;
      }
      .text {
        font-size: $font-size-small;
      }
    }
    .list-box {
      position: absolute;
      left: 0;
      top: 110px;
      right: 0;
      bottom: 0;
      .list-scroll {
        height: 100%;
        overflow: hidden;
        .list-inner {
          padding: 20px 30px;
        }
      }
    }
    .no-result-box {
      position: absolute;
      width: 100%;
      top: 50%;
      transform: translateY(-50%);
    }
  }
</style>



